.container
  max-width: 1200px
  margin: 0 auto
  @media (max-width: 1240px)
    padding: 0 15px


.loading
  position: relative
  &:after
    position: absolute
    content: ''
    background-image: url("data:image/gif;base64,")
    width: 60px
    left: 50%
    margin-left: -30px
    height: 60px
    top: 50%
    margin-top: -30px
