  max-height: 20px;
}
.show-home__link-inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: #F2F6ED;
  border-radius: 60px;
  min-width: 220px;
  transform: translateX(58px);
  opacity: 0;
  transition: opacity 0.2s;
}
.show-home__link {
  display: block;
  padding: 20px;
  color: #535353;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}

.mobile-down {
  display: none;
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 1000;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
