.category
  @include section-indent-margin
  &--submenu
    display: none
    position: absolute
    min-width: 1200px
    margin-bottom: 0
    @media #{$notebook-small}
      min-width: 970px
    //&:before
    //  position: absolute
    //  content: ''
    //  width: calc(100vw + 100%)
    //  height: calc(200vh + 100%)
    //  background-color: rgba(83, 83, 83, .5)
    //  left: -100%
    //  top: -100%
    //  z-index: 1
    //  pointer-events: none
    & .container
      position: relative
      z-index: 1000
  &__title
    @include section-title
    @include text-position
    margin-bottom: 30px

  &__wrapper
    &--submenu
      display: flex
      background-color: white
      padding: 40px
      border-radius: 12px
      box-shadow: 0px 4px 51px 0px rgba(0, 0, 0, 0.25)
      backdrop-filter: blur(7.5px)

      & .category__item-icon-inner
        margin-bottom: 154px
      & .category__trigger-inner
        align-items: flex-start
      & .category__trigger
        padding: 0
        color: rgba(83, 83, 83, 1)
        background: inherit
        &.tabs-active
          background-color: inherit
          color: rgba(122, 160, 75, 1)


  &__trigger-inner
    display: flex
    align-items: center
    justify-content: center
    gap: 20px
    margin-bottom: 60px
    @media #{$notebook-small}
      margin-bottom: 50px
    @media #{$tablet-small}
      flex-direction: column
      background: rgba(122, 160, 75, .3)
      border-radius: 20px
      padding: 6px
      gap: 0
      margin-bottom: 40px
    @media #{$mobile}
      margin-bottom: 30px
    &--submenu
      flex-direction: column

  &__trigger
    @include button-padding(15px, 34px)
    border-radius: 60px
    background: rgba(122, 160, 75, 0.10)
    @include text-custom(14px,400,rgba(122, 160, 75, 1))
    transition: all .4s
    @media #{$tablet-small}
      width: 100%
      background: none
      padding: 10px 30px
    @media #{$mobile}
      padding: 6px 24px
    &.tabs-active
      color: #FFFFFF
      background: rgba(122, 160, 75, 1)

  &__content-inner
    display: flex
    flex-wrap: wrap
    position: fixed
    left: -999px
    top: -999px
    z-index: -1
    height: 1px
    width: 1px
    visibility: hidden
    opacity: 0
    pointer-events: none
    transition: opacity .3s
    gap: 20px
    &.tabs-pane-show
      position: static
      z-index: 1
      height: 100%
      width: 100%
      visibility: visible
      opacity: 1
      pointer-events: auto

  &__item
    display: flex
    flex-direction: column
    justify-content: space-between
    height: 100%
    width: calc(50% - 10px)
    padding: 30px
    border-radius: 12px
    background-color: #FCF7F4
    border: 1px solid #FCF7F4
    transition: border .4s
    background-image: url("../images/category/categori-1.png")
    @supports (background-image: url("../images/category/categori-1.webp"))
      background-image: url("../images/category/categori-1.webp")
    background-size: 70%
    background-position: right bottom
    background-repeat: no-repeat
    @media #{$mobile}
      flex-direction: row
      padding: 26px 16px
      min-height: 260px
      width: 100%
      flex-wrap: wrap
      background-size: 230px
    &:hover
      border: 1px solid #B99D7B
      & .category__item-icon-inner
        background-color: rgba(185, 157, 123, 1)
    &--overlay
      flex: 1 auto
      background-size: contain

  &__item-icon-inner
    display: flex
    align-items: center
    justify-content: center
    width: 40px
    height: 40px
    border-radius: 50%
    background-color: rgba(185, 157, 123, .1)
    margin-bottom: 204px
    transition: background-color .4s
    @media #{$notebook}
      margin-bottom: 160px
    @media #{$notebook-small}
      margin-bottom: 120px
    @media #{$tablet}
      margin-bottom: 80px
    @media #{$mobile}
      margin-bottom: 0
      order: 2

  &__item-icon
    max-width: 20px
    max-height: 20px

  &__item-subtitle
    @include text-subtitle
    margin-bottom: 12px

  &__item-fake-link
    @include text-custom(16px, 400, $title-second-color)
    @media #{$tablet-small}
      font-size: 14px

  &__sub-inner
    display: flex
    flex-direction: column
    justify-content: space-between
    margin-right: 100px

  &__sub-link
    color: white
    @include button-padding(20px,30px)
    @include button
    white-space: nowrap